
angular.module('GithubApp').run(['$templateCache', function ($templateCache) {
	$templateCache.put('views/about.html', '<div class="row"> <h1>{{pageName}}</h1> </div> <div class="row"> <p>&nbsp;</p> <p>This application is based on: <i>AngularJS GitHub Contributors</i> (<a href="http://daha.github.com/angularJS-github-contributors/" target="_blank">App</a> | <a href="https://github.com/daha/angularJS-github-contributors/" target="_blank">GitHub</a>)<p> <br> <p>Application (<a href="https://github.com/erkobridee/angularjs-github-info" target="_blank">Code on GitHub</a> | <a href="https://github.com/erkobridee/angularjs-github-info/blob/master/LICENSE" target="_blank">Modified BSD License</a>) </p> <ul class="techs"> <li><b>Interface:</b> <a href="http://twitter.github.com/bootstrap/" target="_blank">Twitter Bootstrap</a> using <a href="http://twitter.github.com/bootstrap/scaffolding.html#responsive" target="_blank">responsive desgin</a></li> <li><b>Engine:</b> <a href="http://angularjs.org/" target="_blank">AngularJS</a> (<a href="http://docs.angularjs.org/guide/" target="_blank">Guide</a>)</li> <li><b>Features:</b> List all public repositories, public gists and contributors repository from some GitHub user</li> <li> <b>Data Access:</b> <a href="http://developer.github.com/" target="_blank">GitHub API</a> </li> <li> <b>URLs examples accessing GitHub API:</b> <ul> <li>User Info: <a href="https://api.github.com/users/erkobridee" target="_blank">https://api.github.com/users/erkobridee</a></li> <li>User public gists: <a href="https://api.github.com/users/erkobridee/gists" target="_blank">https://api.github.com/users/erkobridee/gists</a></li> <li>User public repositories: <a href="https://api.github.com/users/erkobridee/repos" target="_blank">https://api.github.com/users/erkobridee/repos</a></li> <li>Repository Info: <a href="https://api.github.com/repos/erkobridee/Twitter-Analyzer" target="_blank">https://api.github.com/repos/erkobridee/Twitter-Analyzer</a></li> <li>Repository Contributors: <a href="https://api.github.com/repos/erkobridee/Twitter-Analyzer/contributors" target="_blank">https://api.github.com/repos/erkobridee/Twitter-Analyzer/contributors</a></li> </ul> </li> </ul> </div>');
	$templateCache.put('views/components/navbar.html', '<div> <div class="navbar navbar-fixed-top"> <div class="navbar-inner"> <div class="container"> <a class="btn btn-navbar" data-toggle="collapse" data-target=".nav-collapse"> <span class="icon-bar"></span> <span class="icon-bar"></span> <span class="icon-bar"></span> </a> <a class="brand" href="#">{{title}}</a> <div class="nav-collapse"> <ul class="nav"> <li class="{{nav.searchNotActive}}active"><a ng-href="#{{nav.searchUrlPath}}"><i class="icon-search icon-white"></i> Search</a></li> <li class="{{nav.aboutNotActive}}active"><a href="#/about"><i class="icon-question-sign icon-white"></i> About</a></li> </ul> </div> </div> </div> </div> <a href="https://github.com/erkobridee/angularjs-github-info" ng-show="nav.showForkBelt"><img class="leftForkBelt" src="https://s3.amazonaws.com/github/ribbons/forkme_left_gray_6d6d6d.png" alt="Fork me on GitHub"></a> </div> ');
	$templateCache.put('views/components/pane.html', '<div class="tab-pane" ng-class="{active: selected}" ng-transclude> </div>');
	$templateCache.put('views/components/tabs.html', '<div class="tabbable"> <ul class="nav nav-tabs"> <li ng-repeat="pane in panes" ng-class="{active:pane.selected}"> <a href="" ng-click="select(pane)">{{pane.title}}</a> </li> </ul> <div class="tab-content" ng-transclude></div> </div>');
	$templateCache.put('views/github/repo-contributors.html', '<hr> <div> <h3>Project Contributors ({{contributors.data.length}})</h3> <ul class="contributors"> <li class="contributor" ng-repeat="contributor in contributors.data"> <a class="avatar" ng-href="#/github/{{contributor.login}}/" title="{{contributionsTitle(contributor)}}"> <img alt="{{contributor.login}}\'s avatar" ng-src="{{contributor.avatar_url}}"/> <span class="name">{{contributor.login}}</span> <span class="contributions">{{contributor.contributions}}</span> </a> </li> </ul> </div>');
	$templateCache.put('views/github/repo-info.html', '<ul class="breadcrumb"> <li><i class="icon-search"></i> <a href="#">Search Other</a> <span class="divider">/</span></li> <li><a href="#github/{{repoInfo.data.owner.login}}">{{repoInfo.data.owner.login}}</a> <span class="divider">/</span></li> <li class="active">{{repoInfo.data.name}}</li> <p class="pull-right"><a href="{{repoInfo.data.html_url}}" target="_blank">View on GitHub</a> <i class="icon-eye-open"></i></p> </ul> <div class="row-fluid user"> <div class="login"> <div> <span class="avatar user-avatar"> <img ng-src="{{repoInfo.data.owner.avatar_url}}" alt="{{repoInfo.data.owner.login}}\'s avatar"/> </span> </div> <div> <h2>{{repoInfo.data.owner.login}}</h2> <h2><small>({{repoInfo.data.name}})</small></h2> </div> </div> <!-- @begin: watchers / forks --> <div class="pull-right well info"> <ul> <li> <h1>{{repoInfo.data.watchers}}</h1> <ng-pluralize count="repoInfo.data.watchers" when="watchForms"></ng-pluralize> </li> <li> <h1>{{repoInfo.data.forks}}</h1> <ng-pluralize count="repoInfo.data.forks" when="forkForms"></ng-pluralize> </li> </ul> </div> </div> <div ng-show="repoInfo.data.description"> <hr> <h4>Description:</h4> <span class="repo-desc">{{repoInfo.data.description}}</span> </div> ');
	$templateCache.put('views/github/repo.html', '<div ng-hide="repoInfo.data"> <p style="text-align:center"><img src="img/loading.gif"></p> </div> <div ng-show="repoInfo.data"> <div class="row" ng-include="\'views/github/repo-info.html\'"></div> <div class="row" ng-include="\'views/github/repo-contributors.html\'"></div> </div>');
	$templateCache.put('views/github/user-gists.html', '<div ng-show="gists.data.length" class="container"> <ul class="unstyled"> <li class="row repo" ng-repeat="gist in gists.data | orderBy:\'-updated_at\'"> <ul class="name"> <li> <h4><a ng-href="{{gist.html_url}}/" target="_blank">{{getFile(gist.files).filename}}</a></h4> <span class="visible-desktop line-desc" ng-show="gist.description">{{gist.description}}</span> </li> </ul> <!-- @begin: file infos --> <div class="pull-right stats"> <ul> <li class="lang"> <b>{{getFile(gist.files).language}}</b> </li> </ul> </div> </li> </ul> </div> ');
	$templateCache.put('views/github/user-info.html', '<ul class="breadcrumb"> <li><i class="icon-search"></i> <a href="#">Search Other</a> <span class="divider">/</span></li> <li class="active">{{user.data.login}}</li> <p class="pull-right"><a href="{{user.data.html_url}}" target="_blank">View on GitHub</a> <i class="icon-eye-open"></i></p> </ul> <div class="row-fluid user"> <div class="login"> <div> <span class="avatar user-avatar"> <img ng-src="{{user.data.avatar_url}}" alt="{{user.data.login}}\'s avatar"/> </span> </div> <div> <h2>{{user.data.login}}</h2> <h2 ng-show="user.data.name"><small>({{user.data.name}})</small></h2> </div> </div> <!-- @begin: public repos / followers --> <div class="pull-right well info"> <ul> <li> <h1>{{user.data.public_repos}}</h1> <ng-pluralize count="user.data.public_repos" when="publicRepoForms"></ng-pluralize> </li> <li> <h1>{{user.data.followers}}</h1> <ng-pluralize count="user.data.followers" when="followerForms"></ng-pluralize> </li> </ul> </div> </div> ');
	$templateCache.put('views/github/user-repos.html', '<div ng-show="repos.data.length" class="container"> <ul class="unstyled"> <li class="row repo" ng-repeat="repo in repos.data | orderBy:\'-watchers\'"> <ul class="name"> <li> <h4><a href="#/github/{{repo.full_name}}/">{{repo.name}}</a></h4> <span class="visible-desktop line-desc" ng-show="repo.description">{{repo.description}}</span> </li> </ul> <!-- @begin: repository infos --> <div class="pull-right stats"> <ul> <li class="lang"> <b>{{repo.language}}</b> </li> <li class="watchers"> <b>{{repo.watchers}}</b> <span> <ng-pluralize count="repo.watchers" when="watchForms"></ng-pluralize> </span> </li> <li class="forks"> <b>{{repo.forks}}</b> <span> <ng-pluralize count="repo.forks" when="forkForms"></ng-pluralize> </span> </li> </ul> </div> </li> </ul> </div> ');
	$templateCache.put('views/github/user.html', '<div class="container"> <div ng-hide="user.data"> <p style="text-align:center"><img src="img/loading.gif"></p> </div> <div class="row" ng-show="user.data.message"> <ul class="breadcrumb"> <li><i class="icon-search"></i> <a href="#">Search Other</a></li> </ul> <div class="userNotFound"></div> <p style="text-align:center">{{user.data.message}}</p> </div> <!-- @begin: user found --> <div ng-show="user.data.login"> <div class="row" ng-include="\'views/github/user-info.html\'"></div> <div class="row"> <tabs> <pane title="Public Repositories {{checkLength(repos.data)}}"> <div ng-include="\'views/github/user-repos.html\'"></div> </pane> <pane title="Public Gists {{checkLength(gists.data)}}"> <div ng-include="\'views/github/user-gists.html\'"></div> </pane> </tabs> </div> </div> </div>');
	$templateCache.put('views/search.html', '<div class="span12"> <div class="span8 offset2"> <h3>Find Public Repositories and Gists:</h3> <form class="well form-search" ng-submit="searchAction()"> <span>User: </span> <input type="text" class="input-medium search-query" placeholder="ex.: erkobridee" ng-model="searchField"> <button type="submit" class="btn"><i class="icon-search"></i> Search</button> </form> </div> </div> ');
}]);